.fixed_headers {
  border-collapse: collapse;
  margin: 50px;
}
.fixed_headers thead {
  display: block;
  width: 1500px;
  background-color: lightsalmon;
  text-align: left;
}
.fixed_headers tbody {
  display: block;
  width: 1500px;
  height: 500px;
  overflow-y: auto;
}
.fixed_headers tbody tr td,
.fixed_headers thead tr th {
  min-width: 375px;
  max-width: 375px;
  text-align: left;
  padding: 0;
}
.fixed_headers tbody tr:nth-child(even) {
  background-color: lightblue;
}
