.navBar {
  height: 50px;
  background-color: lightskyblue;
  top: 0;
  position: sticky;
  border-bottom: 5px solid salmon;
  margin-bottom: 20px;
}

.navBar ul li {
  display: inline-block;
  width: 25%;
  align-content: center;
  text-align: center;
  padding-top: 10px;
}

.navBar ul {
  border: 0px;
  margin: 0px;
  padding: 0px;
  background-color: lightskyblue;
}

.special {
  text-decoration: none;
}

.navBar a {
  color: whitesmoke;
}

.navBar a:hover {
  color: lightseagreen;
}

.navBarSearchBox {
  color: lightseagreen;
  background-color: lightgray;
}
.navBarSearchBox:focus {
  background-color: gray;
  color: lightsalmon;
}
